import counter from "@/components/counter/counter.vue"
import contact from "@/components/contact/contact.vue"
export default {
	components:{
		counter
	},
	data() {
		return {
			title: 'Hello',
			product:null,
			num17:5
		}
	},
	onLoad() {
		this.getPar()
	},
	methods: {
		getPar(){
			let _this = this;
			this.$ajax("/module/getModuleInfo","POST",{code:'nav'}).then((res)=>{
				console.log(res);
				_this.product = res
			})
		},
		toUrl(url){
			uni.navigateTo({
				"url":url
			})
		},
		cancel(){
			uni.switchTab({
				"url":"/pages/address/address"
			})
		},
		toBussness(){
			uni.showToast({
				"title":"功能尚未开发，敬请期待"
			})
		},
		setNum(num17){
			this.num17 = num17
			console.log(this.num17);
		},
		pay(){
			this.$ajax("/move/buy","POST",{moduleCode:"nav",count:this.num17}).then((res)=>{
					if(res){
						uni.requestPayment({
							provider: "wxpay",
							timeStamp:res.timeStamp,
							nonceStr:res.nonceStr,
							package:res.package,
							signType:res.signType,
							paySign:res.paySign,
							success:function(){
								uni.navigateTo({
									"url":"/pages/address/msdh/index"
								})
							},
							fail:function(){
								uni.showToast({
									"title":"取消支付",
									"icon":"none"
								})
							}
						})
					}else{
						uni.showToast({
							"title":"服务器错误，请稍后再试",
						})
					}
				})
			}
		
	},
	computed:{
		total(){
			if(this.product.price){
				let total = this.num17 * this.product.price;
				if(parseInt(this.num17/30) > 0){
					total = total - parseInt(this.num17/30)*20;
				}
				return total;
			}
			
		}
	}
}
